<template>
  <div class="box1">
        <!-- <h2>这是饼图</h2> -->
        <div id="myPieOne" style="width:350px; height:350px;"></div>
  </div>
</template>

<script>
  import echarts from 'echarts'
    export default {
        data () {
            return {
                author: 'Sally',
                create_time:'2018-4-26',
                charts: '',
                opinion:['直接访问','邮件营销','联盟广告','视频广告','搜索引擎'],
                opinionData:[
                  {value:335, name:'直接访问'},
                  {value:310, name:'邮件营销'},
                  {value:234, name:'联盟广告'},
                  {value:135, name:'视频广告'},
                  {value:1548, name:'搜索引擎'}
                ]
            }
        },
        methods:{
            drawPie(id){
               this.charts = echarts.init(document.getElementById(id))
               this.charts.setOption({
                 tooltip: {
                   trigger: 'item',
                   formatter: '{a}<br/>{b}:{c} ({d}%)'
                 },
                 legend: {
                   orient: 'vertical',
                   x: 'left',
                   data:this.opinion
                 },
                 series: [
                   {
                     name:'访问来源',
                     type:'pie',
                     radius:['50%','70%'],
                     avoidLabelOverlap: false,
                     label: {
                       normal: {
                         show: false,
                         position: 'center'
                       },
                       emphasis: {
                         show: true,
                         textStyle: {
                           fontSize: '30',
                           fontWeight: 'blod'
                         }
                       }
                     },
                     avoidLabelOverlap:true,
                     labelLine: {
                       normal: {
                         show: false
                       }
                     },
                     data:this.opinionData
                   }
                 ]
               })
            }
        },
      //调用
        mounted(){
            console.log('This pie messange :The author is '+this.author+', create time is '+this.create_time)
            this.$nextTick(function() {
                this.drawPie('myPieOne')
            })
        }
    }
</script>
<style >
.box1{
  width: 350px;
  height: 350px;
  padding: 0;
}
</style>



